@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@600&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  height: 100%;
  box-sizing: border-box;
}

body {
  height: 100%;
  font-family: "Source Sans Pro", sans-serif;
  background-color: #4158d0;
  background-image: linear-gradient(
    43deg,
    #4158d0 0%,
    #c850c0 46%,
    #ffcc70 100%
  );
}

.container {
  max-width: 600px;
  padding: 3rem 1rem;
  margin-bottom: 20px;
  background-color: #fffffe;
  display: grid;
  justify-items: center;
  align-items: center;
  border-radius: 5px;
  box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2);
}

.textStyle {
  font-size: 1.4rem;
  line-height: 2.5rem;
  letter-spacing: 0.05rem;
  color: #4a5568;
  border-bottom: 3px solid #dddddd;
  margin-bottom: 20px;
  padding-bottom: 10px;
}

.container > p {
  font-size: 1.3rem;
  line-height: 2.5rem;
  letter-spacing: 0.05rem;
  color: #718096;
}

.spanStyle {
  text-transform: uppercase;
  background: #718096;
  color: #cbd5e0;
  padding: 5px 10px;
  border: 1px solid #718096;
  border-radius: 5px;
  margin-right: 10px;
  font-size: 14px;
}

.wrapper {
  text-align: center;
}

.joke__container {
  display: grid;
  justify-items: center;
}

.title {
  background: #4a5568;
  text-align: center;
  padding: 0.8rem 2rem;
  color: #edf2f7;
  text-transform: uppercase;
  letter-spacing: 0.2rem;
  margin-bottom: 2rem;
}

.loader {
  width: 80px;
  height: 80px;
  border: 5px solid #cbd5e0;
  border-radius: 50%;
  border-top: 5px solid #3182ce;
  margin: 20px auto;
  -webkit-animation: spinner 1.5s linear infinite;
          animation: spinner 1.5s linear infinite;
}

@-webkit-keyframes spinner {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes spinner {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.btn {
  font-size: 1rem;
  border: none;
  box-shadow: none;
  margin: 20px auto;
  outline: none;
  background: #bee3f8;
  color: #2c5282;
  padding: 15px 30px;
  text-transform: uppercase;
  letter-spacing: 0.04rem;
  box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.4);
  cursor: pointer;
  outline: 5px solid #2c5282;
  transition: all 0.3s ease-in-out;
}

.btn:hover {
  outline-offset: 5px;
}

